define('services/DialogService',['./module'], function (module) {
    window.URLRE =/^(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&amp;:/~\+#]*[\w\-\@?^=%&amp;/~\+#])?/;
    module.factory("DService",['$modal',function ($modal) {
            this.alert = function (title) {
                var args=arguments;
                title = title || "";
                var modalInstance = $modal.open({
                templateUrl:"../../../html/partials/template.html",
                size:"lg",    
                controller:"ConfigCtrl",
                resolve:{
                        source:function(){
                            return args;
                        }
                    }                
                });  
                return modalInstance.result;
            };
            return this;
    }]).controller("ConfigCtrl",['$scope','$modalInstance','source',function($scope,$modalInstance,source){
            $scope.active = {state:-1,hover:-1,name:[""]};
            $scope.testdata = [{title:"2014-10-10",value:10000},{title:"2014-1-13",value:399999}];
            
            $scope.select = {flip:[{id:0,name:""},{id:1,name:""}],
                             unit:[{id:0,sym:""},{id:1,sym:"$"}],
                             unit_text:"",
                             fre:[1,3,5,8,10,20,30,60],
                             colors:["red","yellow","black","green"],
                             figures:[{id:0,name:""},
                                       {id:1,name:""},
                                       {id:2,name:""}],
                            choosed:{flip:0,color:2,preunit:null,fre:2,unit:null}};//
            
            $scope.arrow = {default:110,step:150};
            $scope.linkdata = [{title:"hello world",link:"http://www.hao123.com"},{title:"hello world",link:"http://www.hao123.com"}];
            $scope.extLink = {id:"",link:""};
            $scope.config={data:{main:null,sub:[]},figure:{}};//
            $scope.coms = [{index:0,name:"",img:"../src/img/flip.png",
                            subs:[{id:0,img:"../src/img/flip_lg_1.png"},{id:1,img:"../src/img/flip_lg_2.png"}]},
                           {index:1,name:"",img:"../src/img/piechart.png",subs:[{id:0,img:"../src/img/piechart.png"},{id:1,img:"../src/img/circle.png"}]},
                           {index:2,name:"",img:"../src/img/linechart.png"},
                           {index:3,name:"",img:"../src/img/quota.png",subs:[{id:0,img:"../src/img/quota.png"}]},
                          {index:4,name:"",img:"../src/img/link.png",subs:[{id:0,img:"../src/img/link.png"}]},
                           {index:5,name:"",img:"../src/img/tag.png"},
                           {index:6,name:"",img:"../src/img/ext.png",subs:[{id:0,img:"../src/img/ext.png"}]}];
            $scope.ok = function (state) {
                switch(state){
                    case 0://
                        var param = $scope.config;
                        break;
                    case 1://
                        
                        break;
                    case 3://
                        
                        break;
                    case 4://
                        if(!$scope.active.name[state] || $scope.active.name[state]=="") {
                             $("input#stateName").focus(); 
                            return;
                        }
                    case 6://
                        if(!URLRE.test(($scope.extLink.link).trim())){// 
                            $("#extLink").focus();
                            return;
                        }
                        break;
                }
                $modalInstance.close(param);
              };

              $scope.cancel = function (state) {
                $modalInstance.dismiss('cancel');
              };
        
              $scope.back = function () {//
                $scope.active.state = -1;  
                //$scope.cancel();
              };         
    }]);
});
